import React, { Fragment } from "react";

const Timer = ({timerYears, timerDays, timerHours, timerMinutes, timerSeconds}) => {
    return <Fragment>
        <section className="timer-container">
            <section className="timer-section">
                <div className="clock">
                    <span>I have been coding for</span>
                    <p className="timer">{timerYears}<span className="timer-span">years,</span></p>
                    <p className="timer">{timerDays}<span className="timer-span">days,</span></p>
                    <p className="timer">{timerHours}<span className="timer-span">hours,</span></p>
                    <p className="timer">{timerMinutes}<span className="timer-span">minutes, and</span></p>
                    <p className="timer">{timerSeconds}<span className="timer-span">seconds.</span></p>
                </div>
            </section>
        </section>
    </Fragment>
};

// Timer.defaultProps = {
//     timerYears: 10,
//     timerDays:10,
//     timerHours: 10,
//     timerMinutes: 10,
//     timerSeconds: 10,
// }

export default Timer;